.app-container {
  display: flex;
  padding-bottom: 50px;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.app-header-text {
  margin-top: 100px;
  position: relative;
  
}

.app-centered-flex {
  margin: 30px;
  
  background-color: white;
  width: 1000px;
  height: 500px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

.embla {
  overflow: hidden;
}

.embla__container {
  display: flex;
}

.embla__slide {
  flex: 0 0 100%;
  min-width: 0;
}

.embla__slide img {
  border-radius: 10px;
  width: 1000px;
  height: 500px;
}

.app-welcome-box {
  
  display: flex;
  width: 1000px;
  height: 200px;
}

.app-welcome-textbox {
  width: 300px;
}

.app-welcome-text {
  font-size: 24px;
  font-family: gotham_bookregular !important;
  font-weight: 600;
  color: #605e5e;
}

.app-welcome-companyText {
  font-family: gotham_lightregular !important;
  margin-top: 0px;
  color: #8b0000;
  font-weight: 200;
  font-size: 50px;
  line-height: 52px;
}

.app-welcome-descriptionbox {
  border-left: 2.5px #ebecec solid;
}

.app-welcome-description {
  color: #687078;
  font-size: 18px;
  font-family: gotham_bookregular;
  font-weight: 400;
}

.app-welcome-paragraphbox {
  padding: 10px;
}

.homepageText {
  text-align: center;
  font-size: 50px;
  font-family: gotham_bookregular !important;
  font-weight: 600;
  color: #605e5e;
  border-bottom: 2.5px #8b0000 solid;
}

.app-projects-box {
  background-color: #f7f7f7;
  margin: 30px;
  width: 100%;
  height: 500px;
}

.app-projects-textbox {
  display: flex;
  padding-bottom: 25px;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100px;
}

.app-projects-text {
  display: inline;
  text-align: center;
  font-size: 40px;
  font-family: gotham_bookregular !important;
  font-weight: 600;
  color: #605e5e;
  border-bottom: 2.5px #8b0000 solid;
}

.app-projects-cardbox {
  padding-top: 25px;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.app-projects-flex {
  display: flex;
  justify-content: center;
  align-content: center;
}

.app-projects-flexbox {
  border-radius: 10px;
  position: relative;
  background-color: #444654;
  height: 200px;
  width: 300px;
}

.app-projects-names {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #8b0000;
  color: white;
  text-align: center;
  font-family: gotham_bookregular !important;
  font-weight: 600;
  font-size: 28px;
}

.cardImages {
  border-radius: 10px;
  width: 300px;
  height: 200px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}
